    rerender(): void {
        this.dtElements.forEach(( dtElement: DataTableDirective ) => {
            dtElement.dtInstance.then(( dtInstance: DataTables.Api ) => {
                // Do your stuff
                dtInstance.destroy();
            } );
        } );
    }







    ngOnInit(): void {
    }
    lrissueDataMethod() {

        this.dtOptionsIssueReport = {
            //};
            //columns is used to for export and others starts



            //columns is used to for export and others endss


            // the below code is for button export starts
            dom: 'Bfrtip',
            /*buttons: [
                      'excel', 'print'
                  ],*/
            buttons: [
                {
                    extend: 'print',
                    text: '<i class="fas fa-print"> Print</i>',
                    titleAttr: 'Print',
                    title: this.changeDataTableTitile(),
                    messageTop: this.chnageDataTableMessageTop(),
                    autoPrint: true,
                    footer: true,

                }, {
                    extend: 'excel',
                    text: '<i class="fas fa-file-excel"> Excel</i>',
                    titleAttr: 'Excel',
                    footer: true,
                    title: this.changeDataTableTitile(),
                    messageTop: this.chnageDataTableMessageTopExcel(),
                    exportOptions: {
                        columns: ':visible'
                    }
                }
            ],



            // the below code is for button export ends


            //place holder in search/filter in datatable starts
            language: {
                search: "_INPUT_",
                searchPlaceholder: "Search..."
            },
            //place holder in search/filter in datatable ends



            processing: true,
            //scroll in datatable starts
            responsive: true,
            "scrollX": true,
            "scrollY": 380,
            "scrollCollapse": true,
            //this used to hide paggination and content like showing 1 to 3 of 20 entries Starts
            "paging": false,
            "info": false,
            //this used to hide paggination and content like showing 1 to 3 of 20 entries Starts
            //scroll in datatable ends
            "footerCallback": function( row, data, start, end, display ) {
                var api = this.api(), data;

                // Remove the formatting to get integer data for summation
                var intVal = function( i ) {
                    return typeof i === 'string' ?
                        +i.replace( /[\$,]/g, '' ) * 1 :
                        typeof i === 'number' ?
                            i : 0;
                };

                //                count starts
                var columnData = api
                    .column(
                    0 )
                    .data();
                // Update footer
                $( api.column( 0 ).footer() ).html(
                    columnData.count()
                );
                var total = api
                    .column( 4 )
                    .data()
                    .reduce( function( a, b ) {
                        return intVal( a ) + intVal( b );
                    }, 0 );
                // Update footer
                $( api.column( 4 ).footer() ).html(
                    total
                );

            }

        }

    }

